import { TextField } from "@mui/material";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import { onAnalyticsSearch } from "../../../Redux/Admin/Analytics/AnalyticsThunk";
import { GetAccessToken } from "../../../Redux/Auth/AuthSelector";
import styles from './AnalyticsFilter.module.css';

export const AnalyticsFilter = () => {
    const dispatch = useDispatch();

    // Local store
    const {register, handleSubmit} = useForm<FormTypes>();

    // Global store
    const access_token = useSelector(GetAccessToken);

    // Functions
    const onFormSubmit = (data: FormTypes) => {
        const preparedData = {
            min: data.min,
            max: data.max,
        };
        dispatch(onAnalyticsSearch(preparedData, access_token));
    };

    return (
        <div className={styles.main_block}>
            <form onSubmit={handleSubmit(onFormSubmit)}>
                <TextField
                    className={styles.min_input}
                    type='number'
                    id="standard-basic"
                    label="min"
                    {...register('min')}
                    variant="standard" />
                <TextField
                    className={styles.min_input} 
                    id="standard-basic" 
                    type='number' 
                    {...register('max')}
                    label="max" 
                    variant="standard" />
                <button>Искать</button>
            </form>
        </div>
    );
};

type FormTypes = {
    min: string;
    max: string;
}