import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
// Components
import { Header } from "../../../Components/Header/Header";
import { OperatorEditModule } from "../../../Components/Modal/OperatorEditModal/OperatorEditModal";
import { SearchOperators } from "../../../Components/Search/SearchOperators/SearchOperators";
// State
import { GetOperators, GetOperatorsCount, GetOperatorsIsLoading, GetSearchedOperators } from "../../../Redux/Admin/Operators/OperatorsSelector";
import { GetAccessToken } from "../../../Redux/Auth/AuthSelector";
// Thunks
import { deleteOperatorThunk, getOperatorsThunk } from "../../../Redux/Admin/Operators/OperatorsThunk";
// Functions
import { createPages } from "../../../utils/PagesCreator";
// Types
import {operatorsDataType} from '../../../Redux/Admin/Operators/OperatorsReducer';
// Images
import packmanLoader from '../../../Images/packman.gif';
// Styles
import styles from './Operators.module.css';


export const Operators = () => {
    const dispatch = useDispatch();

    // Local store
    const [deleted_operator_id, setDeleted_operator_id] = useState(-1);
    const [update_operator_modal_shown, setUpdate_operator_modal_shown] = useState(false);
    const [clicked_id, setClicked_id] = useState(-1);
    const [current_page, setCurrent_page] = useState(1);
    const pages: number[] = [];

    // Global state
    const access_token = useSelector(GetAccessToken);
    const loader = useSelector(GetOperatorsIsLoading);
    let operators = useSelector(GetOperators);
    const totalCount = useSelector(GetOperatorsCount);
    const searchedOperators: any = useSelector(GetSearchedOperators);

    // Functions
    const pagesCount = Math.round(totalCount/15);
    createPages(pages, pagesCount, current_page);

    const onEditButtonClick = (id: number) => {
        for (let i = 0; i < operators.length; i++) {
            if (operators[i].id === id) {
                setClicked_id(i);
                setUpdate_operator_modal_shown(true);
            }
        }
    };

    const onDeleteButtonClick = (id: number) => {
        setDeleted_operator_id(id)
        dispatch(deleteOperatorThunk(deleted_operator_id, access_token));
    };

    operators
        ?.filter((operator: operatorsDataType) => Number(operator.id) === deleted_operator_id)
        .map((index: number) => operators = operators.splice(index, 1))

    useEffect(() => {
        dispatch(getOperatorsThunk(access_token, current_page));
    }, [access_token, current_page, dispatch]);

    
    const allOperators = operators.map((operator: any) => {
        return (
            <div className={styles.operators_block}>
                <div>{operator.full_name}</div>
                <div>{operator.email}</div>
                <div>{operator.phone}</div>
                <div>{operator.id}</div>
                <div>
                    <button onClick={() => onDeleteButtonClick(operator.id)} className={styles.delete_button}>Pozmak</button>
                </div>
                <div>
                    <button onClick={async () => {setClicked_id(operator.id); onEditButtonClick(operator.id)}} className={styles.edit_button}>Üýtgetmek</button>
                </div>
            </div>
        )});
    let searchingOperators;
    searchingOperators = searchedOperators?.map((operator: any) => { 
        return (
            <div className={styles.operators_block}>
                <div>{operator.full_name}</div>
                <div>{operator.email}</div>
                <div>{operator.phone}</div>
                <div>{operator.id}</div>
                <div>
                    <button onClick={() => onDeleteButtonClick(operator.id)} className={styles.delete_button}>Pozmak</button>
                </div>
                <div>
                    <button onClick={() => {setClicked_id(operator.id); onEditButtonClick(operator.id)}} className={styles.edit_button}>Üýtgetmek</button>
                </div>
            </div>
        )
    })

    const headerItems = ['Ady', 'Email', 'Nomer', 'Id', 'Pozmak', 'Üýtgetmek'];
        return (
            <div className={styles.main_block}>
                {update_operator_modal_shown 
                ? <OperatorEditModule 
                    tr={operators[clicked_id]} 
                    isModalOpen={update_operator_modal_shown}
                    changeModalOpen={setUpdate_operator_modal_shown}/>
                : <>

                    {loader
                        ? <img className={styles.packman_loader} alt='Loader' src={packmanLoader}/>
                        : <div>
                            <SearchOperators />
                            <div className={styles.content_block}>
                                <Header headerItems={headerItems}/>
                                {(searchedOperators) ? (searchingOperators) : (allOperators)}
                                <div className={styles.pages}>
                                {pages.map((page, index) => {
                                    return (<span key={index}
                                        onClick={() => {setCurrent_page(page)}}
                                        className={styles.page}>{page}
                                    </span>)
                                })}
                                </div>
                            </div>
                            </div>
                    }
                    </>
                    
                }
            </div>
        )
}