import { AxiosResponse } from 'axios';
import { api } from '../../../api';
import { Thunk } from '../../types';
import { AnalyticsActions, AnalyticsActionsType } from './AnalyticsReducer';

export const getAnalyticsDataThunk =
    (token: string): Thunk<AnalyticsActionsType> =>
        async dispatch => {
            try {
                if (token === "") return;
                const response: AxiosResponse<GetResposeType> = await api.getApi({
                    url: `admin/all-real-estate-statistics`,
                    token: token,
                    withCredentials: true,
                    contentType: false,
                });
                dispatch(AnalyticsActions.addAnalyticsData(response.data.rows));
            } catch (error: any) {
                console.log(error.response.message);
            }
        };


        
export const onAnalyticsSearch =
(searchData: SearchData, token: string): Thunk<AnalyticsActionsType> =>
    async dispatch => {
        try {
            if (token === "") return;
            const response: AxiosResponse<GetResposeType> = await api.getApi({
                url: `admin/all-real-estate-statistics?price.min=${searchData.min}&price.max=${searchData.max}`,
                token: token,
                withCredentials: true,
                contentType: false,
            });
            dispatch(AnalyticsActions.addAnalyticsData(response.data.rows));
        } catch (error: any) {
            console.log(error.response.message);
        }
    };



    

// Types
type GetResposeType = {
    rows: [ {id: number; count: string; absolute_name: string;} ];
};

type SearchData = {
    min: string;
    max: string;
};