import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Switch from "react-switch";
// Components
import { Header } from "../../../Components/Header/Header";
import { SpecificationEditModal } from "../../../Components/Modal/SpecificationEditModal/SpecificationEditModal";
import { SearchSpecifications } from "../../../Components/Search/SearchSpecifications/SearchOperators/SearchSpecifications";
// Thunks
import { changeSearchedSpecificationActivationThunk, changeSpecificationActivationThunk, getAllSpecificationsThunk } from "../../../Redux/Admin/Specifications/SpecificatiosThunk";
// State
import { GetSearchedSpecifications, GetSpecifications, GetSpecificationsTotalCount } from "../../../Redux/Admin/Specifications/SpecificationsSelector";
import { GetAccessToken } from "../../../Redux/Auth/AuthSelector";
// Images
import spinner from '../../../Images/spinner.gif';
import packman from '../../../Images/packman.gif';
// Types
import { SpecificationsDataType } from "../../../Redux/Admin/Specifications/SpecificationsReducer";
// Functions
import { createPages } from "../../../utils/PagesCreator";
// Styles
import styles from './Specifications.module.css';


// Types
type TranslationsType = {
    language_id: number;
    name: string;
}


export const Specifications = () => {
    const dispatch = useDispatch();

    // Local store
    const [is_loading, setId_loading] = useState(false);
    const [clicked_element_id, setClicked_element_id] = useState(0);
    const [current_page, setCurrent_page] = useState(1);
    const [is_switch_loading, setIs_switch_loading] = useState(0);
    const [specification_modal_shown, setSpecification_modal_shown] = useState(false);

    // Global store
    const access_token = useSelector(GetAccessToken);
    const specifications = useSelector(GetSpecifications);
    const totalCount = useSelector(GetSpecificationsTotalCount);
    
    const searchedSpecifications = useSelector(GetSearchedSpecifications);

    const onSpecificationActivationChange = (id: number, boolValue: boolean) => {
        setIs_switch_loading(id);
        dispatch(changeSpecificationActivationThunk(id, !boolValue, setIs_switch_loading, access_token));
    };
    
    const onSearchedSpecificationActivationChange = (id: number, boolValue: boolean) => {
        setIs_switch_loading(id);
        dispatch(changeSearchedSpecificationActivationThunk(id, !boolValue, setIs_switch_loading, access_token));
    };

    let pages: number[] = [];
    const pagesCount = Math.round(totalCount/15);
    createPages(pages, pagesCount, current_page);
    console.log(pages, pagesCount, current_page);
    
    const [add_specification_modal_shown, setAdd_specification_modal_shown] = useState(false);


    useEffect(() => {
        setId_loading(true);
        dispatch(getAllSpecificationsThunk(setId_loading, access_token, current_page));
    }, [access_token, dispatch, current_page]);

    let searchedSpecificationsRender;
    searchedSpecificationsRender = searchedSpecifications?.map((searchedSpecification: any) => {
        return (
            <div className={styles.specifications_block}>
                <div>{searchedSpecification.id}</div>
                <div>{searchedSpecification.absolute_name}</div>
                <div>
                    {searchedSpecification.translations
                        .filter((translation: TranslationsType) => translation.language_id === 1)
                        .map((translation: TranslationsType) => translation.name)
                    }
                </div>
                <div>
                    {searchedSpecification.translations
                        .filter((translation: TranslationsType) => translation.language_id === 2)
                        .map((translation: TranslationsType) => translation.name)
                    }
                </div>
                <div>{(searchedSpecification.is_required) ? 'Dogry' : 'Ýalan'}</div>
                <div>{searchedSpecification.is_multiple ? 'Dogry' : 'Ýalan'}</div>
                {(is_switch_loading === searchedSpecification.id)
                    ? <img src={spinner} alt='Loader' className='w-10 h-10'/>
                    : <Switch 
                        checked={searchedSpecification.is_active}
                        className='z-0'
                        onChange={() => onSearchedSpecificationActivationChange(searchedSpecification.id, searchedSpecification.is_active)}>
                    </Switch>
                }
                <button className={styles.edit_button} onClick={() => {setClicked_element_id(searchedSpecification.id); setSpecification_modal_shown(true)}}>Üýtgetmek</button>
            </div>
        );
    })


    const specification = specifications.map((specification: SpecificationsDataType) => {
        return (
            <div className={styles.specifications_block}>
                <div>{specification.id}</div>
                <div>{specification.absolute_name}</div>
                <div>
                    {specification.translations
                        .filter((translation: TranslationsType) => translation.language_id === 1)
                        .map((translation: TranslationsType) => translation.name)
                    }
                </div>
                <div>
                    {specification.translations
                        .filter((translation: TranslationsType) => translation.language_id === 2)
                        .map((translation: TranslationsType) => translation.name)
                    }
                </div>
                
                <div>{(specification.is_required) ? 'Dogry' : 'Ýalan'}</div>
                <div>{specification.is_multiple ? 'Dogry' : 'Ýalan'}</div>
                {(is_switch_loading === specification.id) 
                    ? <img src={spinner} alt='Loader' className='w-10 h-10'/> 
                    : <Switch 
                        className='cursor-pointer z-0' checked={specification.is_active}
                        onChange={() => onSpecificationActivationChange(specification.id, specification.is_active)}>
                    </Switch>
                    }
                <button className={styles.edit_button} onClick={() => {setClicked_element_id(specification.id); setSpecification_modal_shown(true)}}>Üýtgetmek</button>
            </div>
        );
    });

    const headerItems = ['Id', 'Absolute ady', 'Türkmençe', 'Rusça', 'Hokman', 'Birnäçe', 'Activasiýa', 'Üýtgetmek'];

    return (
            <div className={styles.main_block}>
                {is_loading 
                    ? <img alt='Loading' className={styles.packman_loader} src={packman}/>
                    : <div>
                    <SearchSpecifications isModalOpen={add_specification_modal_shown} changeModalOpen={setAdd_specification_modal_shown}/>
                    <Header headerItems={headerItems}/>
                    <div>
                        {searchedSpecifications ? searchedSpecificationsRender : specification}
                        {(clicked_element_id !== 0)
                            ? <SpecificationEditModal
                                isModalOpen={specification_modal_shown} 
                                changeModalOpen={setSpecification_modal_shown} 
                                id={clicked_element_id} />
                            : null}
                        {pages.map((page, index) => {
                                    return (<span key={index}
                                        onClick={() => {setCurrent_page(page)}}
                                        className={styles.page}>{page}
                                    </span>)
                                })}
                    </div>
                    
                </div>
                }
            </div>
                
            
    );
}