export const SearchNamesById = (estatesArray: any, array: string[]) => {
    estatesArray.map((estateArray: any) => {
        (estateArray.id === 1) 
            ? array[0] = 'Продаётся' 
            : (estateArray.id === 2) ? array[1] = 'Продано'
            : (estateArray.id === 3) ? array[2] = 'Сдаётся в аренду'
            : (estateArray.id === 4) ? array[3] = 'Сдано в аренду' : console.log('fewrwr');
    })
};
